
// sass 의 유용성 1. 변수로 지정이 가능
$메인칼라 : black;  
.red {
  color : $메인칼라;
}


.my-alert4 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(226, 214, 148);
  padding: 20px;
  border-radius: 5px;
  margin-bottom: 10px;
  max-width: 500px;
  width: 100%;
}


.my-alert p {
  margin-bottom: 0;
}


.wow-enter {   //애니메이션 시작 때 적용할 CSS
  opacity: 0;
}

.wow-enter-active {   // 애니메이션 동작 때 적용할 CSS
  opacity: 1;
  transition: all 700ms;
}